<template>
  <div class="charts-container">
    <div class="charts-header">
      <el-radio-group v-model="currentChart" size="large">
        <el-radio-button label="progress">进度趋势</el-radio-button>
        <el-radio-button label="scatter">散点图</el-radio-button>
        <el-radio-button label="sunburst">旭日图</el-radio-button>
        <el-radio-button label="bar">柱状图</el-radio-button>
        <el-radio-button label="pie">饼图</el-radio-button>
      </el-radio-group>
    </div>
    <div ref="chartContainer" class="chart-item"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: 'TaskChart',
  props: {
    tasks: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      chartInstance: null,
      currentChart: 'progress'
    }
  },
  mounted() {
    this.initChart();
    window.addEventListener('resize', this.resizeChart);
  },
  beforeUnmount() {
    if (this.chartInstance) {
      this.chartInstance.dispose();
      this.chartInstance = null;
    }
    window.removeEventListener('resize', this.resizeChart);
  },
  methods: {
    initChart() {
      if (!this.chartInstance) {
        this.chartInstance = echarts.init(this.$refs.chartContainer);
      }
      
      let option;
      switch (this.currentChart) {
        case 'progress':
          option = this.getProgressOption();
          break;
        case 'scatter':
          option = this.getScatterOption();
          break;
        case 'sunburst':
          option = this.getSunburstOption();
          break;
        case 'bar':
          option = this.getBarChartOption();
          break;
        case 'pie':
          option = this.getPieChartOption();
          break;
        default:
          option = this.getProgressOption();
      }
      
      this.chartInstance.setOption(option, true);
    },

    // 原有的进度趋势图
    getProgressOption() {
      return {
        title: {
          text: '任务进度趋势',
          left: 'center'
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: this.tasks.map(task => task.deadline)
        },
        yAxis: {
          type: 'value',
          max: 100
        },
        series: [
          {
            name: '完成度',
            type: 'line',
            data: this.tasks.map(task => task.completionRate),
            smooth: true,
            markPoint: {
              data: [
                { type: 'max', name: '最大值' },
                { type: 'min', name: '最小值' }
              ]
            }
          }
        ]
      };
    },

    // 新增散点图
    getScatterOption() {
      const data = this.tasks.map(task => [task.priority, task.completionRate]);
      return {
        title: {
          text: '任务优先级与完成度关系',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: function(params) {
            return `优先级: ${params.value[0]}<br/>完成度: ${params.value[1]}%`;
          }
        },
        xAxis: {
          name: '优先级',
          type: 'value',
          max: 3
        },
        yAxis: {
          name: '完成度',
          type: 'value',
          max: 100
        },
        series: [{
          type: 'scatter',
          data: data,
          symbolSize: 20,
          itemStyle: {
            color: function(params) {
              const value = params.value[1];
              if (value > 80) return '#67C23A';
              if (value > 50) return '#E6A23C';
              return '#F56C6C';
            }
          }
        }]
      };
    },

    // 新增旭日图
    getSunburstOption() {
      const priorityData = [1, 2, 3].map(priority => {
        const tasks = this.tasks.filter(task => task.priority === priority);
        return {
          name: `优先级 ${priority}`,
          value: tasks.length,
          children: tasks.map(task => ({
            name: `截止日期: ${task.deadline}`,
            value: 1,
            itemStyle: {
              color: task.completionRate > 50 ? '#67C23A' : '#F56C6C'
            }
          }))
        };
      });

      return {
        title: {
          text: '任务优先级分布',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        series: [{
          type: 'sunburst',
          data: priorityData,
          radius: ['20%', '90%'],
          itemStyle: {
            borderRadius: 7,
            borderWidth: 2,
            borderColor: '#fff'
          },
          label: {
            rotate: 'radial'
          }
        }]
      };
    },

    getBarChartOption() {
      return {
        title: {
          text: '柱状图示例',
          left: 'center'
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: ['A', 'B', 'C', 'D', 'E']
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          type: 'bar',
          data: [10, 20, 30, 40, 50]
        }]
      };
    },

    getPieChartOption() {
      return {
        title: {
          text: '饼图示例',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        series: [{
          type: 'pie',
          radius: '50%',
          data: [
            { value: 1048, name: 'A' },
            { value: 735, name: 'B' },
            { value: 580, name: 'C' },
            { value: 484, name: 'D' },
            { value: 300, name: 'E' }
          ]
        }]
      };
    },

    resizeChart() {
      this.chartInstance?.resize();
    }
  },
  watch: {
    currentChart() {
      this.initChart();
    },
    tasks: {
      handler() {
        this.initChart();
      },
      deep: true
    }
  }
};
</script>

<style scoped>
.charts-container {
  margin: 2rem auto;
  padding: 2rem;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.charts-header {
  margin-bottom: 2rem;
  text-align: center;
}

.chart-item {
  width: 100%;
  height: 400px;
}
</style>